#imageInput[type="file"] {
  appearance: none;
  color: #989daa;
  background: #f2f2f200;
  border: 2px solid #e9eaee;
  border-radius: 10px;
  width: 100%;
  height: 150px;
  padding: 20px;
  font-size: 14px;
  transition: all 1s;
  position: relative;
}

#customUploadButton {
  color: #fff;
  cursor: pointer;
  background-color: #16b1f89f;
  border: 2px solid #16b1f89f;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 40%;
  padding: 20px;
  font-size: 14px;
  font-weight: 600;
  transition: all .5s;
  display: flex;
}

#customUploadButton:hover {
  background-color: #16b1f8;
  border: 2px solid #16b1f8;
  width: 60%;
  height: 50%;
}

#imageInput::file-selector-button {
  color: #fff;
  cursor: pointer;
  background-color: #16b1f89f;
  border: 2px solid #16b1f89f;
  border-radius: 10px;
  width: 50%;
  height: 40%;
  padding: 20px;
  font-size: 14px;
  font-weight: 600;
  transition: all .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#imageInput::file-selector-button:hover {
  background-color: #16b1f8;
  border: 2px solid #16b1f8;
  width: 60%;
  height: 50%;
}

#uploadButton {
  color: #fff;
  cursor: pointer;
  background-color: #16b1f800;
  border: 2px solid #989daaa2;
  border-radius: 10px;
  width: 100%;
  height: 80px;
  padding: 20px;
  font-size: 16px;
  font-weight: 600;
  transition: all .5s;
}

#uploadButton:hover {
  background-color: #4bda87c0;
}

